
.containerText h1 {
    position: relative;
    display: block;
    width: fit-content;
    font-size: 4rem;
    color: transparent;
    animation: text_reveal .5s ease forwards;
    animation-delay: 1.5s;
}

.hero {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 50px;
    justify-content: flex-start;
}

.containerText span {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background-color: crimson;
    animation: text_reveal_box 1s ease;
    animation-delay: .3s;
}


.containerText h1:nth-child(1) {
    animation-delay: 1.5s;
}

.containerText h1:nth-child(2) {
    animation-delay: 2.5s;
}

.containerText h1:nth-child(3) {
    animation: text_reveal_name .5s ease forwards;
    animation-delay: 3.5s;
}

.containerText h1:nth-child(1) span {
    animation-delay: 1s;
}

.containerText h1:nth-child(2) span {
    animation-delay: 2s;
}

.containerText h1:nth-child(3) span {
    animation-delay: 3s;
}

@media only screen and (min-width: 768px) {
    h1 {
        font-size: 7rem;
    }
}

@keyframes text_reveal_box {
    50% {
        width: 100%;
        left: 0;
    }
    100% {
        width: 0;
        left: 100%
    }
}

@keyframes text_reveal {
    100% {
        color: white;
    }
}

@keyframes text_reveal_name {
    100% {
        color: crimson;
        font-weight: 700;
    }
}
